@import "../../../assets/variables";

.box {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 388px;
  padding: 16px 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  @media screen and (max-width: 1024px) {
    padding: 4px 0;
  }

  @media screen and (max-width: 767px) {
    max-width: 288px;
  }

  @media screen and (max-width: 480px) {
    max-width: 100%;
  }

  &:before {
    content: '';
    width: 17px;
    border-bottom: 1px solid #D0D0D8;
  }

  &:after {
    content: '';
    width: 17px;
    border-bottom: 1px solid #D0D0D8;
  }
}

.triangle {
  width: 0;
  height: 0;
  border-top: 35px solid transparent;
  border-bottom: 35px solid transparent;
  border-right: 21px solid #D0D0D8;
  position: relative;
  cursor: pointer;

  @media screen and (max-width: 1024px) {
    border-top: 28px solid transparent;
    border-bottom: 28px solid transparent;
  }

  &:after {
    content: '';
    width: 0;
    height: 0;
    border-top: 35px solid transparent;
    border-bottom: 35px solid transparent;
    border-right: 21px solid $light-color;
    position: absolute;
    top: -35px;
    left: 1px;

    @media screen and (max-width: 1024px) {
      border-top: 28px solid transparent;
      border-bottom: 28px solid transparent;
      top: -28px;
    }
  }

  &:last-child {
    transform: rotate(180deg);
  }
}

.content {
  width: 100%;
  height: 70px;
  padding: 0 5px;
  background-color: $light-color;
  border-top: 1px solid #D0D0D8;
  border-bottom: 1px solid #D0D0D8;
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  @media screen and (max-width: 1024px) {
    height: 56px;
    font-size: 18px;
  }

  @media screen and (max-width: 767px) {
    font-size: 16px;
  }
}

.text {
  font-family: $main-font;
  color: $dark-color;
  font-size: 20px;
}

.numbering {
  font-family: $main-font;
  font-weight: 600;
  color: $base-color;
  font-size: 20px;
  padding-right: 8px;
}